import React, {Component} from 'react';
import {Card, WingBlank, WhiteSpace} from 'antd-mobile';
import util from '../file/util';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // eslint-disable-next-line react/no-unused-state
      data: []
    };
  }
  componentWillMount() {
  }
  componentDidMount() {
    this.getJSON();
  }

  getJSON() {
    const data = util.data || [];
    this.setState({
      // eslint-disable-next-line react/no-unused-state
      data
    });
  }

  getFiled(code) {
    return util.filed[code];
  }

  setTemplate() {
    return (
      this.state.data.map((m, n) => (
        <WingBlank size="lg" key={n}>
          <WhiteSpace size="lg" />
          <Card>
            <Card.Header
              title={m.title}
              thumb=""
            />
            <Card.Body className="menu-list">
              <img src={m.img} />
              <div>
                <label>{this.getFiled('engine')}</label>
                <span>{m.engine}</span>
              </div>
              <div>
                <label>{this.getFiled('tyre')}</label>
                <span>{m.tyre}</span>
              </div>
              <div>
                <label>{this.getFiled('manned')}</label>
                <span>{m.manned}</span>
              </div>
              <div>
                <label>{this.getFiled('setting')}</label>
                <span>{m.setting}</span>
              </div>
              <div>
                <label>{this.getFiled('chassis')}</label>
                <span>{m.chassis}</span>
              </div>
              <div>
                <label>{this.getFiled('out')}</label>
                <span>{m.out}</span>
              </div>
              <div>
                <label>{this.getFiled('inner')}</label>
                <span>{m.inner}</span>
              </div>
              <div>
                <label>{this.getFiled('totalKG')}</label>
                <span>{m.totalKG}</span>
              </div>

              <div>
                <label>{this.getFiled('allowKG')}</label>
                <span>{m.allowKG}</span>
              </div>
              <div>
                <label>{this.getFiled('preparation')}</label>
                <span>{m.preparation}</span>
              </div>

              <div>
                <label>{this.getFiled('license')}</label>
                <span>{m.license}</span>
              </div>
              <div>
                <label>{this.getFiled('brake')}</label>
                <span>{m.brake}</span>
              </div>
            </Card.Body>
            {/* <Card.Footer content="欢迎致电" extra={<div>欢迎致电</div>} /> */}
          </Card>
          <WhiteSpace size="lg" />
        </WingBlank>
      ))
    );
  }

  render() {
    //const temp = this.setTemplate();
    return (
      <div>123</div>
    );
  }
}

export default App;



